<template>
	<div class="clc">
		<div class="box">
			<div class="clock">
				<div class="hour"><div class="hr" id="hr"></div></div>
				<div class="min"><div class="mn" id="mn"></div></div>
				<div class="sec"><div class="sc" id="sc"></div></div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	mounted() {
		const deg = 6;
		const hr = document.querySelector('#hr');
		const mn = document.querySelector('#mn');
		const sc = document.querySelector('#sc');

		function start() {
			let day = new Date();
			let h = day.getHours() * 30;
			let m = day.getMinutes() * deg;
			let s = day.getSeconds() * deg;
			hr.style.transform = `rotateZ(${h + m / 12}deg)`;
			mn.style.transform = `rotateZ(${m}deg)`;
			sc.style.transform = `rotateZ(${s}deg)`;
		}
		start();
		setInterval(start, 1000);
	}
};
</script>

<style scoped>
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.clc{
	width: 100%;
	height: 400px;
}
.box {
	position: relative;
	left: calc(50% - 100px);
	top: calc(50% - 160px);
	width: 280px;
	height: 280px;
	backdrop-filter: blur(25px);
	border-radius: 50%;
	border: 1px solid rgba(255, 255, 255, 0.5);
}

.clock {
	position: absolute;
	z-index: 120;
	top: 10px;
	left: 10px;
	right: 10px;
	bottom: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: radial-gradient(transparent, rgba(255,255,255,0.4)), url('http://www.dhb3.com/image/clock.png');
	background-size: contain;
	border-radius: 50%;
	backdrop-filter: blur(12px);
	border: 1px solid rgba(255, 255, 255, 0.9);
	border-bottom: none;
	border-right: none;
	box-shadow: -10px -10px 20px rgba(255, 255, 255, 0.1), 10px 10px 20px rgba(0, 0, 0, 0.1);
}

.clock::before {
	content: '';
	position: absolute;
	z-index: 410;
	width: 15px;
	height: 15px;
	background: #280326;
	border-radius: 50%;
	z-index: 100;
}

.hour,
.min,
.sec {
	position: absolute;
}

.hour,
.hr {
	width: 166px;
	height: 166px;
}

.min,
.mn {
	width: 190px;
	height: 190px;
}

.sec,
.sc {
	width: 230px;
	height: 230px;
}

.hr,
.mn,
.sc {
	display: flex;
	justify-content: center;
	position: relative;
	z-index: 99;
	border-radius: 50%;
}

.hr::before {
	content: '';
	position: absolute;
	width: 7px;
	height: 95px;
	background: #ff105e;
	z-index: 97;
	border-radius: 6px;
}

.mn::before {
	content: '';
	position: absolute;
	width: 4px;
	height: 120px;
	background: #676767;
	z-index: 98;
	border-radius: 6px;
}

.sc::before {
	content: '';
	position: absolute;
	width: 2px;
	height: 150px;
	background: #ababab;
	z-index: 99;
	border-radius: 6px;
}
</style>
