var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"new_1"},[_c('div',{staticClass:"newDetails"},[_c('div',{staticClass:"title"},[_c('div',{staticClass:"position"},[_c('i',{staticClass:"el-icon-map-location",staticStyle:{"margin-right":"5px"}}),_c('span',[_vm._v("您所在的位置 ：")]),_c('el-breadcrumb',{attrs:{"separator-class":"el-icon-arrow-right"}},[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首页")]),_c('el-breadcrumb-item',{attrs:{"to":{ path: '/news' }}},[_vm._v("游戏新闻")]),_c('el-breadcrumb-item',[_vm._v("新闻详情")])],1)],1),_c('h1',[_vm._v("PS5面临黄牛、芯片短缺等问题 长期生态受到威胁")]),_c('p',{staticClass:"time"},[_vm._v("发布时间：2020-12-17 11:48:16")]),_c('div',{staticStyle:{"clear":"both"}}),_c('el-divider',[_c('i',{staticClass:"el-icon-reading"})])],1),_c('div',{staticClass:"mainText"},[_c('p',[_vm._v("索尼推出的PS5主机无疑是今年的热门产品之一，不过这款游戏机的发售却遭到了黄牛党的破坏，他们对市面上稀缺产品的抢购，威胁着PS5主机的长期生态。")]),_c('p',[_vm._v("黄牛党一直是游戏行业所面临的一大挑战，他们以官方零售价购入设备，再以更高的价格转售。而因为疫情的原因，次世代主机产量缩减的同时，线上销售也成为了大趋势，而这也给了黄牛们可乘之机，他们利用先进的自动程序在线上商城来抢购PS5和Xbox Series主机。")]),_vm._m(0),_c('p',[_vm._v("而发售初期遭遇的困境，可能会影响索尼吸引玩家和开发商使用新平台的能力，进而损害其未来几年的利润。新主机发售应该引发消费者竞相购买，而开发商利用新主机的性能推出新游戏，双方互相促进以达成一个良性循环，但索尼可能遭遇相反的局面。")]),_c('p',[_vm._v("投资研究机构Morningstar（晨星）的分析师Kazunori Ito认为：“PS5主机可能会错过进入软硬件良性循环的关键机会，该平台的峰值可能会较低，总收益也不会像我们预期的那么高。”")]),_c('p',[_vm._v("从目前各方公开的数据来看，黄牛对新主机的抢购囤积，已经对PS5的软硬件销售比造成了影响。对于新主机而言，这一比例应该在1左右，即每一位购买新主机的玩家都会购入至少一款游戏。这一数字非常重要，因为PS5主机是亏本出售的，而作为软件的游戏则可以带来盈利。")]),_c('p',[_vm._v("但目前的情况是，PS5主机销售一空，但游戏销量却并未跟上，据Fami通近期公开的数据显示，PS5主机在日本的发售并未促进软件销量的激增：")]),_vm._m(1),_c('p',[_vm._v("※黑色为发售前4周主机销量，蓝色为销量前三位的游戏销量；左为PS5，右为Switch")]),_c('p',[_vm._v("从目前的情况来看，似乎每卖出三台PS5主机，才会售出一款游戏，这也是黄牛囤货的有力证据。PS5在日本的首月销量约为21.3万，而前三名的游戏销量不足6.3万（不包括数字版）。相比之下，Switch上市时日本市场前四周售出50万台，排名前三的游戏的销量大致相同。")]),_c('p',[_vm._v("Ace Research Institute的分析师Hideki Yasuda表示：“即便考虑上数字版软件的销量，PS5主机的实际（软硬件）销售比也没有那么高，这意味着目前的需求受到了二手商贩的限制。”不过PS5兼容PS4游戏，还预装了《宇航员的娱乐室》，所以玩家可能不会立即购买新游戏。")]),_c('p',[_vm._v("索尼的目标是在3月底前售出760万台PS5主机，以超过上一代游戏机的表现。但疫情已经造成了整个行业供应链的短缺，削弱了索尼、苹果等公司提高产量的能力。包括联发科技在内的主要供应商已经表示，芯片供应在2021年上半年将受到限制。熟悉PS5供应链的消息人士透露，包括电动汽车制造商在内的一些厂商的强劲需求，一直在损耗PS5所使用的一些零部件的产能。")]),_c('p',[_vm._v("知情人士表示，索尼最为担心的点在于，PS5定制处理器的产量仍然不稳定，已经损害了它满足需求的能力。目前索尼可能不得不更多地依靠空运向零售商运送游戏机，这将进一步削减其利润。在正常情况下，空运的成本至少是海运的10倍，现在这种情况下差距可能更大。")]),_c('p',[_vm._v("其中一位知情人士向彭博社透露，索尼最新的出货量预测仍远高于760万，不过已经没有早些时候那么高了。")]),_c('p',[_vm._v("索尼方面拒绝置评黄牛和产量问题，一名发言人表示：“虽然我们没有公布生产相关的细节，但自PS5主机开始大规模生产以来，都没有发生意外情况，我们没有改变PS5的生产数量。”")]),_vm._m(2),_c('p',[_vm._v("彭博社在报道中还指出，日本一家大型发行商被市场的早期反应吓到，因此还就是否推迟PS5游戏的发售举行了内部讨论。")]),_c('p',[_vm._v("现在零售商也在采取措施阻止黄牛党，GameStop直到发货前一小时才向员工通报各大商店的新主机发货情况，以便真正的顾客能够买到。日本的Nojima让员工手工审核每一份订单，以清除黄牛党。然而据估算，黄牛们已经从PS5主机上赚了3500万美元，在Xbox上赚了2400万美元（相关报道）。")]),_c('p',[_vm._v("许多消费者指责索尼没有采取更多措施增加供应，以减少黄牛利润。游戏产业顾问Serkan Toto表示：“PS5面临的真正考验是，即使供应充足，其硬件是否能继续保持畅销。你需要优秀的游戏来说服玩家转向PS5，你需要更多第三方在PS5上发行游戏，你还需要售出更多硬件。”")]),_c('el-divider',[_c('i',{staticClass:"el-icon-collection"})])],1)]),_c('div',{staticClass:"newright"},[_c('clock')],1)])])}
